.gallery {
  box-sizing: border-box;
}

.gallery__heading {
  box-sizing: border-box;
  margin: 0;
  @include bold(24px, 1.25em, $black);
  text-align: center;
  margin: 0 8px 0 8px;
}

.gallery__list {
  box-sizing: border-box;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.gallery__item {
  box-sizing: border-box;
  width: 158px;
  height: 135px;
  margin-bottom: 6px;
  &:nth-child(odd) {
    margin-right: 4px;
  }
  &:nth-child(n+6) {
    margin-bottom: 0;
  }
}

.gallery__image {
  @include responsive-image;
  box-sizing: border-box;
  width: 158px;
  height: 135px;
}
